export default "import { Field } from '@jafar-org/react-form';\n\nconst columnStyle = {\n  width: '400px',\n  maxWidth: '400px',\n  margin: '0 30px 0 0',\n};\n\nfunction getColumn(fieldIds) {\n  return {\n    direction: 'column',\n    style: columnStyle,\n    boxes: fieldIds.map(id => ({ component: Field, props: { id } })),\n  };\n}\n\nexport default [{\n  id: 'personal-information',\n  title: 'Personal Information',\n  boxes: [{\n    direction: 'row',\n    boxes: [\n      getColumn(['firstName', 'lastName']),\n      getColumn(['personalId', 'address'])],\n  }],\n}, {\n  id: 'job-information',\n  title: 'Job Information',\n  boxes: [{\n    direction: 'row',\n    boxes: [\n      getColumn(['department', 'benefits']),\n      getColumn(['level'])],\n  }],\n}, {\n  id: 'raw-data',\n  title: 'Raw Data',\n  boxes: [{\n    direction: 'row',\n    boxes: [\n      getColumn(['id', 'modifier']),\n      getColumn(['creationDate', 'modificationDate'])],\n  }],\n  sections: [{\n    id: 'raw-data-general',\n    title: 'General',\n    boxes: [{\n      direction: 'row',\n      boxes: [\n        getColumn(['id']),\n        getColumn(['modifier'])],\n    }],\n  }, {\n    id: 'raw-data-modification',\n    title: 'Modification',\n    boxes: [{\n      direction: 'row',\n      boxes: [\n        getColumn(['creationDate']),\n        getColumn(['modificationDate'])],\n    }],\n  }],\n}];\n";