export default "import { Field } from '@jafar-org/react-form';\n\nconst columnStyle = {\n  width: '100%',\n  maxWidth: '400px',\n};\n\nfunction getColumn(fieldIds) {\n  return {\n    direction: 'column',\n    style: columnStyle,\n    boxes: fieldIds.map(id => ({ component: Field, props: { id } })),\n  };\n}\n\nexport default [{\n  id: 'personal-information',\n  title: 'Personal Information',\n  boxes: [{\n    direction: 'row',\n    boxes: [\n      getColumn(['firstName', 'lastName', 'personalId', 'address']),\n    ],\n  }],\n}, {\n  id: 'job-information',\n  title: 'Job Information',\n  boxes: [{\n    direction: 'row',\n    boxes: [\n      getColumn(['department', 'benefits', 'level']),\n    ],\n  }],\n}];\n";